import axios from 'axios'
import { getParsedNftAccountsByOwner } from '@nfteyez/sol-rayz'
import { MetadataKey } from '@nfteyez/sol-rayz/dist/config/metaplex'
import { Connection, LAMPORTS_PER_SOL, PublicKey } from '@solana/web3.js'
import { getAssociateTokenAccount, isEqualAddress } from '../utils'
import { KanonNft } from '../typings'

interface ParsedNFT {
  mint: string
  updateAuthority: string
  data: {
    creators: any[]
    name: string
    symbol: string
    uri: string
    sellerFeeBasisPoints: number
  }
  key: MetadataKey
  primarySaleHappened: boolean
  isMutable: boolean
  editionNonce: number
  masterEdition?: string
  edition?: string
}

export const getSolBalance = async (publicKey: PublicKey, connection: Connection) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const balance = await connection.getBalance(publicKey)

  return balance / LAMPORTS_PER_SOL
}

export const getTokenBalance = async (publicKey: PublicKey, connection: Connection, args: { snsMint: PublicKey }) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  let _tokenBalance = 0
  const ata = getAssociateTokenAccount(args.snsMint, publicKey)
  const tokenAccount = await connection.getParsedAccountInfo(ata)
  if (tokenAccount.value)
    _tokenBalance =
      // @ts-ignore
      tokenAccount.value.data.parsed.info.tokenAmount.uiAmount

  return Number(_tokenBalance)
}

export const getKanonNfts = async (
  publicKey: PublicKey,
  connection: Connection,
  args: {
    kanonNftCharity: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const nftData = await getParsedNftAccountsByOwner({
    publicAddress: publicKey.toBase58(),
    connection
  })
  const parsedNfts: ParsedNFT[] = Object.keys(nftData).map((key) => nftData[key])

  const parsedKanonNftData = parsedNfts.filter(
    (nft) =>
      nft.data.creators.length === 1 &&
      isEqualAddress(nft.data.creators[0].address, args.kanonNftCharity) &&
      nft.data.creators[0].share === 100
  )

  const arrMetadata: string[] = []
  await Promise.all(
    parsedKanonNftData.map(async (nft) => {
      try {
        const resp = await axios.get(nft.data.uri)
        arrMetadata.push(resp.data.image)
      } catch (e) {
        console.log(e)
        arrMetadata.push('')
      }
    })
  )

  return parsedKanonNftData.map((nft, idx) => {
    return {
      mint: nft.mint,
      updateAuthority: nft.updateAuthority,
      name: nft.data.name,
      symbol: nft.data.symbol,
      metadataUri: nft.data.uri,
      imageUri: arrMetadata[idx]
    } as KanonNft
  })
}
