import { LAMPORTS_PER_SOL } from '@solana/web3.js'
import axios from 'axios'
import BN from 'bn.js'
import {
  ApiCampaignInfo,
  ApiCampaignMeta,
  SubmitOntologyArgs,
  BuilderActivityInfo,
  ValidatorActivityInfo,
  ApiSubmissionInfo,
  ApiValidationsInfo
} from '../typings'
import { PHRASE_TYPE } from '../utils'

interface ApiResponse {
  data: any
}

export const submitOntology = async (ontology: SubmitOntologyArgs, args: { apiHost: string; apiAuth: string }) => {
  return axios
    .post(
      `${args.apiHost}/submit-ontology`,
      [
        {
          ...ontology,
          mock: false
        }
      ],
      {
        headers: { Authorization: args.apiAuth }
      }
    )
    .then((response: ApiResponse) => {
      if (Array.isArray(response.data) && response.data.length >= 2 && Array.isArray(response.data[1])) {
        const submissionCount = response.data[0] as number
        const ret = response.data[1][0] as string
        if (ret.toLowerCase() === 'false') {
          return {
            isSuccess: false,
            data: 'DUPLICATTION',
            submissionCount
          }
        } else if (ret.toLowerCase() === 'true') {
          return {
            isSuccess: false,
            data: 'CAP_LIMIT',
            submissionCount
          }
        } else {
          return {
            isSuccess: true,
            data: ret,
            submissionCount
          }
        }
      } else {
        return {
          isSuccess: false,
          data: 'NETWORK_ERROR',
          submissionCount: 0
        }
      }
    })
}

export const deleteOntology = async (
  canonical: string,
  args: { apiHost: string; apiAuth: string }
): Promise<boolean> => {
  return axios
    .post(
      `${args.apiHost}/delete-ontology`,
      [
        {
          reference_id: canonical
        }
      ],
      {
        headers: { Authorization: args.apiAuth }
      }
    )
    .then((response: ApiResponse) => {
      if (response.data && Array.isArray(response.data) && response.data[0] !== 'false' && response.data[0] !== false) {
        return true
      } else {
        return false
      }
    })
}

export const checkWhitelist = async (
  wallet: string,
  args: { apiHost: string; apiAuth: string }
): Promise<string | null> => {
  return axios
    .post(
      `${args.apiHost}/check-whitelist`,
      {
        wallet
      },
      {
        headers: { Authorization: args.apiAuth }
      }
    )
    .then((response: ApiResponse) => {
      if (response.data !== null && response.data !== 'null') {
        return response.data as string
      } else {
        return null
      }
    })
    .catch((error: any) => {
      console.log(error)
      return null
    })
}

export const addCampaignMeta = async (
  data: ApiCampaignMeta,
  args: { apiHost: string; apiAuth: string }
): Promise<boolean> => {
  return await axios
    .post(`${args.apiHost}/add-campaign-meta`, data, {
      headers: { Authorization: args.apiAuth }
    })
    .then((res) => {
      if (res.data === true || res.data === 'true') return true
      else return false
    })
}

