import {
  AddressLookupTableProgram,
  Connection,
  PublicKey,
  Signer,
  SYSVAR_CLOCK_PUBKEY,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction
} from '@solana/web3.js'
import BN from 'bn.js'
import {
  createCreateGuildInstruction,
  createGuildStakeNftInstruction,
  createScholarSignInstruction,
  Guild,
  Role
} from '../dyfarm'
import { getAssociateTokenAccount, LOOKUP_PROGRAM_ADDRESS, METADATA_PROGRAM_ADDRESS } from '../utils'

export const createCreateGuildInstructions = async (
  admin: PublicKey,
  connection: Connection,
  guildTitle: string,
  ownerRate: number,
  masterRate: number,
  args: {
    programId: PublicKey
  }
) => {
  if (!admin) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [guildAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:GUILD'), admin.toBuffer(), Buffer.from(guildTitle)],
    args.programId
  )
  const slot = await connection.getSlot()
  const nftTableAccount = AddressLookupTableProgram.createLookupTable({
    authority: pdaAccount,
    payer: admin,
    recentSlot: slot - 5
  })[1]
  const scholarTableAccount = AddressLookupTableProgram.createLookupTable({
    authority: pdaAccount,
    payer: admin,
    recentSlot: slot - 3
  })[1]

  instructions.push(
    createCreateGuildInstruction(
      {
        owner: admin,
        guildAccount,
        pdaAccount,
        nftTableAccount,
        scholarTableAccount,
        lookupProgram: new PublicKey(LOOKUP_PROGRAM_ADDRESS),
        clock: SYSVAR_CLOCK_PUBKEY,
        rent: SYSVAR_RENT_PUBKEY
      },
      {
        guildTitle,
        slot: new BN(slot),
        ownerRate,
        master: admin,
        masterRate
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const createGuildStakeNftInstructions = async (
  admin: PublicKey,
  user: PublicKey,
  connection: Connection,
  guildTitle: string,
  nftMint: PublicKey,
  role: Role,
  args: {
    programId: PublicKey
  }
) => {
  if (!admin) throw new Error('Admin is undefined')
  if (!user) throw new Error('User is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [guildAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:GUILD'), admin.toBuffer(), Buffer.from(guildTitle)],
    args.programId
  )
  const guild = await Guild.fromAccountAddress(connection, guildAccount, 'processed')
  const [userProfile] = PublicKey.findProgramAddressSync([Buffer.from('DYF:PROFILE'), user.toBuffer()], args.programId)
  const masterNft = getAssociateTokenAccount(new PublicKey(nftMint), admin)
  const [nftVault] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:VAULT'), nftMint.toBuffer()],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const [nftMetadata] = PublicKey.findProgramAddressSync(
    [Buffer.from('metadata'), new PublicKey(METADATA_PROGRAM_ADDRESS).toBuffer(), nftMint.toBuffer()],
    new PublicKey(METADATA_PROGRAM_ADDRESS)
  )
  const nftTableAccount = AddressLookupTableProgram.createLookupTable({
    authority: pdaAccount,
    payer: admin,
    recentSlot: Number(guild.nftSlot)
  })[1]
  const scholarTableAccount = AddressLookupTableProgram.createLookupTable({
    authority: pdaAccount,
    payer: admin,
    recentSlot: Number(guild.scholarSlot)
  })[1]

  instructions.push(
    createGuildStakeNftInstruction(
      {
        master: admin,
        guildAccount,
        user,
        userProfile,
        masterNft,
        nftTableAccount,
        scholarTableAccount,
        nftVault,
        farmConfig,
        pdaAccount,
        nftMint,
        nftMetadata,
        lookupProgram: new PublicKey(LOOKUP_PROGRAM_ADDRESS),
        clock: SYSVAR_CLOCK_PUBKEY,
        rent: SYSVAR_RENT_PUBKEY
      },
      {
        guildTitle,
        role
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const createScholarSignInstructions = async (
  admin: PublicKey,
  user: PublicKey,
  connection: Connection,
  guildTitle: string,
  args: {
    programId: PublicKey
  }
) => {
  if (!admin) throw new Error('Admin is undefined')
  if (!user) throw new Error('User is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [guildAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:GUILD'), admin.toBuffer(), Buffer.from(guildTitle)],
    args.programId
  )
  const [userProfile] = PublicKey.findProgramAddressSync([Buffer.from('DYF:PROFILE'), user.toBuffer()], args.programId)

  instructions.push(
    createScholarSignInstruction(
      {
        user,
        admin,
        userProfile,
        guildAccount,
        pdaAccount
      },
      {
        guildTitle
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const getGuild = async (
  admin: PublicKey,
  connection: Connection,
  guildTitle: string,
  args: {
    programId: PublicKey
  }
) => {
  if (!admin) throw new Error('Admin is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const [guildAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:GUILD'), admin.toBuffer(), Buffer.from(guildTitle)],
    args.programId
  )
  const guild = await Guild.fromAccountAddress(connection, guildAccount, 'processed')

  return guild
}
