import { Connection, PublicKey, Signer, SYSVAR_CLOCK_PUBKEY, TransactionInstruction } from '@solana/web3.js'
import BN from 'bn.js'
import { Md5 } from 'ts-md5'
import { MerkleTree } from 'merkletreejs'
import keccak256 from 'keccak256'

import {
  ApiSubmissionInfo,
  ApiValidationsInfo,
  RpcValidationsInfo,
  PUtterance,
  ValidationArgs,
  PHistory
} from '../typings'
import { isEqualAddress, RpcTxnStatus } from '../utils'
import { createValidatePhraseInstruction, Validate } from '../dyfarm'
import { getRpcValidationStatus, getSubmissionsValidationsInfo, rpcBatchValidatePhrase } from '../services'

export const getUtterancesAndHistoriesForValidator = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  campaignAccountPubkey: string,
  latestSubmittedUuids: string[],
  args: { apiHost: string; apiAuth: string; rpcHost: string; programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignAccount = new PublicKey(campaignAccountPubkey)

  const submissionsAndValidationsInfo = await getSubmissionsValidationsInfo(
    campaignAccount.toBase58(),
    publicKey.toBase58(),
    {
      apiHost: args.apiHost,
      apiAuth: args.apiAuth
    }
  )
  const submissionsInfo = submissionsAndValidationsInfo.submissions.filter(
    (submission) => submission.pubkey && submission.submitted
  )
  const validationsInfo = submissionsAndValidationsInfo.validations
  const rpcValidations = await getRpcValidationStatus(publicKey.toBase58(), campaignAccount.toBase58(), {
    rpcHost: args.rpcHost
  })

  const objValidationsInfo: {
    [utterance: string]: PHistory[]
  } = validationsInfo.reduce((all, validation) => {
    const validations: ApiValidationsInfo[] = all[validation.utterance] ?? []
    return {
      ...all,
      [validation.utterance]: validations.slice().concat(validation)
    }
  }, {})
  const objRpcValidations: {
    [utterance: string]: RpcValidationsInfo
  } = rpcValidations.reduce((all, validation) => {
    return { ...all, [validation.utterance]: validation }
  }, {})

  const utterancesChecked: PUtterance[] = []
  const utterancesQueued: PUtterance[] = []
  const utterancesExpired: PUtterance[] = []

  const submissionsChecked: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (!submission.pubkey) return
    const histories = objValidationsInfo[submission.pubkey] ?? []
    const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
    if (!objRpcValidations[submission.pubkey] || history) {
      submissionsChecked.push(submission)
    }
  })
  submissionsChecked.forEach((submission) => {
    const histories = submission.pubkey ? objValidationsInfo[submission.pubkey] ?? [] : []
    const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
    utterancesChecked.push({
      timestamp: submission.timestamp,
      kind: submission.kind,
      data: submission.data,
      canonical: submission.canonical,
      builder: submission.builder,
      submitted: true,
      pubkey: submission.pubkey,
      finish: submission.finish,
      correct: submission.correct,
      incorrect: submission.incorrect,
      head: submission.correct + submission.incorrect,
      history: histories,
      validated: history ? history.vote : undefined,
      confidence: history ? history.confidence : undefined
    })
  })

  const submissionsNotValidatedButRpcFinalizedOrExpired: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (!submission.pubkey) return
    const histories = objValidationsInfo[submission.pubkey] ?? []
    const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
    if (
      objRpcValidations[submission.pubkey] &&
      (objRpcValidations[submission.pubkey].status === RpcTxnStatus.Finalized ||
        objRpcValidations[submission.pubkey].status === RpcTxnStatus.Expired) &&
      !history
    ) {
      submissionsNotValidatedButRpcFinalizedOrExpired.push(submission)
    }
  })
  await Promise.all(
    submissionsNotValidatedButRpcFinalizedOrExpired.map(async (submission) => {
      if (!submission.pubkey) return
      const [historyAccount] = PublicKey.findProgramAddressSync(
        [
          Buffer.from('DYF:CAMPAIGN:PHRASE:LOG'),
          Buffer.from(campaignTitle),
          new PublicKey(submission.pubkey).toBuffer(),
          publicKey.toBuffer()
        ],
        args.programId
      )

      const histories = objValidationsInfo[submission.pubkey] ?? []
      const utterance = {
        timestamp: submission.timestamp,
        kind: submission.kind,
        data: submission.data,
        canonical: submission.canonical,
        builder: submission.builder,
        submitted: true,
        pubkey: submission.pubkey,
        finish: submission.finish,
        correct: submission.correct,
        incorrect: submission.incorrect,
        head: submission.correct + submission.incorrect,
        history: histories
      }

      try {
        const history = await Validate.fromAccountAddress(connection, historyAccount, 'processed')
        utterancesChecked.push({
          ...utterance,
          history: histories.concat({
            timestamp: new BN(history.time).toNumber(),
            utterance: history.phrase.toBase58(),
            validator: history.address.toBase58(),
            vote: history.vote,
            confidence: history.confident,
            pubkey: historyAccount.toBase58()
          }),
          validated: history ? history.vote : undefined,
          confidence: history ? history.confident : undefined
        })
      } catch (e) {
        if (latestSubmittedUuids.includes(objRpcValidations[submission.pubkey].uuid)) {
          utterancesExpired.push({
            ...utterance,
            expired: true
          })
        } else {
          utterancesChecked.push(utterance)
        }
      }
    })
  )

  const submissionsNotValidatedButRpcPending: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (!submission.pubkey) return
    const histories = objValidationsInfo[submission.pubkey] ?? []
    const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
    if (
      objRpcValidations[submission.pubkey] &&
      objRpcValidations[submission.pubkey].status === RpcTxnStatus.Pending &&
      !history
    ) {
      submissionsNotValidatedButRpcPending.push(submission)
    }
  })
  submissionsNotValidatedButRpcPending.forEach((submission) => {
    if (!submission.pubkey) return
    const histories = submission.pubkey ? objValidationsInfo[submission.pubkey] ?? [] : []
    utterancesQueued.push({
      timestamp: submission.timestamp,
      kind: submission.kind,
      data: submission.data,
      canonical: submission.canonical,
      builder: submission.builder,
      submitted: true,
      pubkey: submission.pubkey,
      finish: submission.finish,
      correct: submission.correct,
      incorrect: submission.incorrect,
      head: submission.correct + submission.incorrect,
      history: histories,
      uuid: objRpcValidations[submission.pubkey].uuid
    })
  })

  const utterances = utterancesChecked
    .concat(utterancesQueued)
    .concat(utterancesExpired)
    .sort((a, b) => b.timestamp - a.timestamp || b.data.localeCompare(a.data))
    .reverse()

  return utterances
}

export const createValidatorValidateUtterancesInstructions = (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  validations: ValidationArgs[],
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  let instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [campaignActivity] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), publicKey.toBuffer()],
    args.programId
  )

  instructions = validations.map((validation) => {
    const phraseAccount = new PublicKey(validation.utterance)
    const [logAccount] = PublicKey.findProgramAddressSync(
      [
        Buffer.from('DYF:CAMPAIGN:PHRASE:LOG'),
        Buffer.from(campaignTitle),
        phraseAccount.toBuffer(),
        publicKey.toBuffer()
      ],
      args.programId
    )
    const [builderActivity] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), new PublicKey(validation.builder).toBuffer()],
      args.programId
    )
    return createValidatePhraseInstruction(
      {
        user: publicKey,
        phraseAccount,
        userProfile,
        campaignActivity,
        builderActivity,
        logAccount,
        campaignAccount,
        farmConfig,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      {
        campaignTitle: campaignTitle,
        offchainRef: validation.canonical,
        confident: validation.confidence,
        status: validation.vote
      },
      args.programId
    )
  })
  return { instructions, signers }
}

export const createRpcValidateUtterancesPromises = (
  publicKey: PublicKey,
  connection: Connection,
  rpcAuthToken: string,
  campaignTitle: string,
  batchValidations: ValidationArgs[][],
  args: { programId: PublicKey; rpcHost: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')
  if (!rpcAuthToken || rpcAuthToken === '') throw new Error('RPC Auth Token is undefined')

  const returnArgs = batchValidations.map((_validations) => {
    return [
      rpcAuthToken,
      args.programId.toBase58(),
      campaignTitle,
      publicKey.toBase58(),
      _validations.map((validation) => validation.builder),
      _validations.map((validation) => validation.utterance),
      _validations.map((validation) => validation.canonical),
      _validations.map((validation) => validation.vote),
      _validations.map((validation) => validation.confidence),
      { rpcHost: args.rpcHost }
    ]
  })

  return { promise: rpcBatchValidatePhrase, args: returnArgs }
}

export const createRpcValidateVerifiableUtterancesPromise = async (
  publicKey: PublicKey,
  signMessage: (message: Uint8Array) => Promise<Uint8Array>,
  rpcAuthToken: string,
  campaignTitle: string,
  validations: ValidationArgs[],
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!signMessage) throw new Error('signMessage is undefined')
  if (!rpcAuthToken || rpcAuthToken === '') throw new Error('RPC Auth Token is undefined')

  const messages: (string | number | boolean)[][] = validations.map((validation) => {
    return [
      args.programId.toBase58(),
      campaignTitle,
      publicKey.toBase58(),
      validation.canonical,
      validation.confidence,
      validation.vote
    ]
  })
  const hashes = messages.map((message) => {
    return Md5.hashStr(JSON.stringify(message))
  })

  const tree = new MerkleTree(hashes, keccak256, { sortPairs: true, hashLeaves: true })
  const merkleRaw = tree.getRoot()
  const merkleRoot = Buffer.from(merkleRaw.toString('hex'))

  const signed = await signMessage(merkleRoot)

  const ixBases = hashes.map((hash, idx) => {
    const leaf = keccak256(hash)
    const proof = tree.getProof(leaf, idx)
    const merkleProof: Buffer[] = proof.map((p) => p.data)
    let proofBuffer: Buffer = Buffer.from('')
    for (let i = 0; i < merkleProof.length; i++) {
      proofBuffer = Buffer.concat([proofBuffer, merkleProof[i]])
    }
    const ixData = Buffer.concat([merkleRoot, signed, proofBuffer])
    console.log('ixData: ', ixData.length)
    return Buffer.from(ixData).toString('base64')
  })

  return messages.map((message, idx) => {
    return [
      rpcAuthToken,
      message[0],
      message[1],
      message[2],
      validations[idx].builder,
      validations[idx].utterance,
      message[3],
      message[4],
      message[5],
      ixBases[idx]
    ]
  })
}
