import { Connection, PublicKey, Signer, SYSVAR_CLOCK_PUBKEY, TransactionInstruction } from '@solana/web3.js'
import { Md5 } from 'ts-md5'
import { MerkleTree } from 'merkletreejs'
import keccak256 from 'keccak256'

import BN from 'bn.js'
import {
  ApiSubmissionInfo,
  ApiValidationsInfo,
  SubmitOntologyArgs,
  RpcSubmissionsInfo,
  PUtterance,
  PHistory
} from '../typings'
import { isEqualAddress, PHRASE_TYPE, RpcTxnStatus } from '../utils'
import { createSubmitPhraseInstruction, Offchain, Phrase } from '../dyfarm'
import {
  deleteOntology,
  getRpcSubmissionStatus,
  getSubmissionsValidationsInfo,
  rpcBatchSubmitPhrases,
  submitOntology
} from '../services'

export const getUtterancesAndHistoriesForBuilder = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignAccountPubkey: string,
  latestSubmittedUuids: string[],
  args: { apiHost: string; apiAuth: string; rpcHost: string; programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignAccount = new PublicKey(campaignAccountPubkey)

  const submissionsAndValidationsInfo = await getSubmissionsValidationsInfo(
    campaignAccount.toBase58(),
    publicKey.toBase58(),
    {
      apiHost: args.apiHost,
      apiAuth: args.apiAuth
    }
  )
  const submissionsInfo = submissionsAndValidationsInfo.submissions
  const validationsInfo = submissionsAndValidationsInfo.validations
  const rpcSubmissions = await getRpcSubmissionStatus(publicKey.toBase58(), campaignAccount.toBase58(), {
    rpcHost: args.rpcHost
  })

  const objSubmissionsInfo: {
    [canonical: string]: ApiSubmissionInfo
  } = submissionsInfo.reduce((all, submission) => {
    return { ...all, [submission.canonical]: submission }
  }, {})
  const objValidationsInfo: {
    [utterance: string]: PHistory[]
  } = validationsInfo.reduce((all, validation) => {
    const validations: ApiValidationsInfo[] = all[validation.utterance] ?? []
    return {
      ...all,
      [validation.utterance]: validations.slice().concat(validation)
    }
  }, {})
  const objRpcSubmissions: {
    [canonical: string]: RpcSubmissionsInfo
  } = rpcSubmissions.reduce((all, submission) => {
    return { ...all, [submission.canonical]: submission }
  }, {})

  const utterancesChecked: PUtterance[] = []
  const utterancesPre: PUtterance[] = []
  const utterancesQueued: PUtterance[] = []
  const utterancesExpired: PUtterance[] = []

  const submissionsChecked: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (submission.submitted) submissionsChecked.push(submission)
  })
  submissionsChecked.forEach((submission) => {
    const histories = submission.pubkey ? objValidationsInfo[submission.pubkey] ?? [] : []
    const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
    utterancesChecked.push({
      timestamp: submission.timestamp,
      kind: submission.kind,
      data: submission.data,
      canonical: submission.canonical,
      builder: submission.builder,
      submitted: true,
      pubkey: submission.pubkey,
      finish: submission.finish,
      correct: submission.correct,
      incorrect: submission.incorrect,
      head: submission.correct + submission.incorrect,
      history: histories,
      validated: history ? history.vote : undefined
    })
  })

  const submissionsPre: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (!submission.submitted && !objRpcSubmissions[submission.canonical]) submissionsPre.push(submission)
  })
  submissionsPre.forEach((submission) => {
    utterancesPre.push({
      timestamp: submission.timestamp,
      kind: submission.kind,
      data: submission.data,
      canonical: submission.canonical,
      builder: submission.builder,
      submitted: false
    })
  })

  const submissionsNotSubmittedButRpcFinalizedOrExpired: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (
      !submission.submitted &&
      objRpcSubmissions[submission.canonical] &&
      (objRpcSubmissions[submission.canonical].status === RpcTxnStatus.Finalized ||
        objRpcSubmissions[submission.canonical].status === RpcTxnStatus.Expired)
    )
      submissionsNotSubmittedButRpcFinalizedOrExpired.push(submission)
  })
  await Promise.all(
    submissionsNotSubmittedButRpcFinalizedOrExpired.map(async (submission) => {
      const [phraseAccount] = PublicKey.findProgramAddressSync(
        [Buffer.from('DYF:CAMPAIGN:PHRASE'), campaignAccount.toBuffer(), Buffer.from(submission.canonical)],
        args.programId
      )

      try {
        const phrase = await Phrase.fromAccountAddress(connection, phraseAccount, 'processed')
        if (!isEqualAddress(phrase.builder, publicKey)) {
          throw new Error('RPC Submission Expired')
        } else {
          const histories = objValidationsInfo[phraseAccount.toBase58()] ?? []
          const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
          utterancesChecked.push({
            timestamp: new BN(phrase.timestamp).toNumber(),
            kind: (Object.values(PHRASE_TYPE).find((row) => row.val === Number(phrase.kind)) ?? PHRASE_TYPE.specific)[
              'label'
            ],
            data: objSubmissionsInfo[submission.canonical].data,
            canonical: submission.canonical,
            builder: phrase.builder.toBase58(),
            submitted: true,
            pubkey: phraseAccount.toBase58(),
            finish: phrase.finish,
            correct: phrase.correct,
            incorrect: phrase.incorrect,
            head: phrase.head,
            history: histories,
            validated: history ? history.vote : undefined
          })
        }
      } catch (e) {
        if (latestSubmittedUuids.includes(objRpcSubmissions[submission.canonical].uuid)) {
          utterancesExpired.push({
            timestamp: submission.timestamp,
            kind: submission.kind,
            data: submission.data,
            canonical: submission.canonical,
            builder: submission.builder,
            submitted: false,
            expired: true
          })
        } else {
          utterancesPre.push({
            timestamp: submission.timestamp,
            kind: submission.kind,
            data: submission.data,
            canonical: submission.canonical,
            builder: submission.builder,
            submitted: false
          })
        }
      }
    })
  )

  const submissionsNotSubmittedAndRpcPending: ApiSubmissionInfo[] = []
  submissionsInfo.forEach((submission) => {
    if (
      !submission.submitted &&
      objRpcSubmissions[submission.canonical] &&
      objRpcSubmissions[submission.canonical].status === RpcTxnStatus.Pending
    )
      submissionsNotSubmittedAndRpcPending.push(submission)
  })
  submissionsNotSubmittedAndRpcPending.forEach((submission) => {
    utterancesQueued.push({
      timestamp: submission.timestamp,
      kind: submission.kind,
      data: submission.data,
      canonical: submission.canonical,
      builder: submission.builder,
      submitted: false,
      uuid: objRpcSubmissions[submission.canonical].uuid
    })
  })

  const utterances = utterancesChecked
    .concat(utterancesQueued)
    .concat(utterancesPre)
    .concat(utterancesExpired)
    .sort((a, b) => a.timestamp - b.timestamp || a.data.localeCompare(b.data))
    .reverse()

  return utterances
}

export const createUtteranceByOntology = async (
  publicKey: PublicKey,
  ontology: SubmitOntologyArgs,
  args: { apiHost: string; apiAuth: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')

  const resp = await submitOntology(ontology, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  if (!resp.isSuccess) {
    if (resp.data === 'NETWORK_ERROR') {
      throw new Error('Network Error.')
    } else if (resp.data === 'DUPLICATTION') {
      throw new Error("This utterance already exists in the organizer's knowledge base.")
    } else if (resp.data === 'CAP_LIMIT') {
      throw new Error(
        `${resp.submissionCount} Submissions for ${ontology.utterance_type} already exists. No more are allowed under the submissions cap.`
      )
    } else {
      throw new Error('Something went wrong. Please reload page.')
    }
  }

  const canonical = resp.data
  const utterance: PUtterance = {
    timestamp: Date.now(),
    kind: ontology.utterance_type,
    data: ontology.utterance,
    canonical,
    builder: publicKey.toBase58(),
    submitted: false
  }
  return utterance
}

export const deleteOntologyByCanonical = async (canonical: string, args: { apiHost: string; apiAuth: string }) => {
  if (!canonical || canonical === '') {
    throw new Error('Invalid canonical.')
  }

  const ret = await deleteOntology(canonical, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })

  if (!ret) {
    throw new Error('Ontology submission failed.')
  }

  return ret
}

export const createBuilderSubmitUtterancesInstructions = (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  utterances: PUtterance[],
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  let instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [campaignActivity] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), publicKey.toBuffer()],
    args.programId
  )

  instructions = utterances.map((utterance) => {
    const [phraseAccount] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:CAMPAIGN:PHRASE'), campaignAccount.toBuffer(), Buffer.from(utterance.canonical)],
      args.programId
    )
    return createSubmitPhraseInstruction(
      {
        user: publicKey,
        phraseAccount,
        userProfile,
        campaignActivity,
        campaignAccount,
        farmConfig,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      {
        campaignTitle: campaignTitle,
        offchainRef: utterance.canonical,
        offchainType: Offchain.S3,
        kind: PHRASE_TYPE[utterance.kind.toLowerCase()].val
      },
      args.programId
    )
  })
  return { instructions, signers }
}

export const createRpcSubmitUtterancesPromises = (
  publicKey: PublicKey,
  connection: Connection,
  rpcAuthToken: string,
  campaignTitle: string,
  batchUtterances: PUtterance[][],
  args: { programId: PublicKey; rpcHost: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')
  if (!rpcAuthToken || rpcAuthToken === '') throw new Error('RPC Auth Token is undefined')

  const returnArgs = batchUtterances.map((_utterances) => {
    return [
      rpcAuthToken,
      args.programId.toBase58(),
      campaignTitle,
      publicKey.toBase58(),
      _utterances.map((utterance) => utterance.canonical),
      _utterances.map(() => Offchain.S3),
      _utterances.map((utterance) => PHRASE_TYPE[String(utterance.kind).toLowerCase()].val),
      { rpcHost: args.rpcHost }
    ]
  })

  return { promise: rpcBatchSubmitPhrases, args: returnArgs }
}

export const createRpcSubmitVerifiableUtterancesPromise = async (
  publicKey: PublicKey,
  signMessage: (message: Uint8Array) => Promise<Uint8Array>,
  rpcAuthToken: string,
  campaignTitle: string,
  utterances: PUtterance[],
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!signMessage) throw new Error('signMessage is undefined')
  if (!rpcAuthToken || rpcAuthToken === '') throw new Error('RPC Auth Token is undefined')

  const messages: (string | number)[][] = utterances.map((utterance) => {
    return [
      args.programId.toBase58(),
      campaignTitle,
      publicKey.toBase58(),
      utterance.canonical,
      Offchain.S3,
      PHRASE_TYPE[String(utterance.kind).toLowerCase()].val
    ]
  })
  const hashes = messages.map((message) => {
    return Md5.hashStr(JSON.stringify(message))
  })

  const tree = new MerkleTree(hashes, keccak256, { sortPairs: true, hashLeaves: true })
  const merkleRaw = tree.getRoot()
  const merkleRoot = Buffer.from(merkleRaw.toString('hex'))

  const signed = await signMessage(merkleRoot)

  const ixBases = hashes.map((hash, idx) => {
    const leaf = keccak256(hash)
    const proof = tree.getProof(leaf, idx)
    const merkleProof: Buffer[] = proof.map((p) => p.data)
    let proofBuffer: Buffer = Buffer.from('')
    for (let i = 0; i < merkleProof.length; i++) {
      proofBuffer = Buffer.concat([proofBuffer, merkleProof[i]])
    }
    const ixData = Buffer.concat([merkleRoot, signed, proofBuffer])
    console.log('ixData: ', ixData.length)
    return Buffer.from(ixData).toString('base64')
  })

  return messages.map((message, idx) => {
    return [rpcAuthToken, ...message, ixBases[idx]]
  })
}
