import {
  Connection,
  PublicKey,
  Signer,
  SYSVAR_CLOCK_PUBKEY,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction
} from '@solana/web3.js'
import BN from 'bn.js'
import randomstring from 'randomstring'
import { ApiValidationsInfo, PUtterance, PHistory, CreateCampaignArgs, UpdateCampaignArgs } from '../typings'
import { getAssociateTokenAccount, isEqualAddress, LAMPORTS_PER_USDC, LOOKUP_PROGRAM_ADDRESS, SNS_PAIR } from '../utils'
import {
  createClaimCampaignInstruction,
  createCreateCampaignInstruction,
  createUpdateCampaignInstruction,
  FarmConfig
} from '../dyfarm'
import { getAllCampaignTitles, getSubmissionsValidationsInfo } from '../services'

export const getUtterancesAndHistoriesForArchitect = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignAccountPubkey: string,
  args: { apiHost: string; apiAuth: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignAccount = new PublicKey(campaignAccountPubkey)

  const submissionsAndValidationsInfo = await getSubmissionsValidationsInfo(
    campaignAccount.toBase58(),
    publicKey.toBase58(),
    {
      apiHost: args.apiHost,
      apiAuth: args.apiAuth
    }
  )
  const submissionsInfo = submissionsAndValidationsInfo.submissions
  const validationsInfo = submissionsAndValidationsInfo.validations

  const objValidationsInfo: {
    [utterance: string]: PHistory[]
  } = validationsInfo.reduce((all, validation) => {
    const validations: ApiValidationsInfo[] = all[validation.utterance] ?? []
    return {
      ...all,
      [validation.utterance]: validations.slice().concat(validation)
    }
  }, {})

  const submissionsChecked = submissionsInfo.filter((submission) => submission.submitted)

  const utterances: PUtterance[] = submissionsChecked
    .map((submission) => {
      const histories = submission.pubkey ? objValidationsInfo[submission.pubkey] ?? [] : []
      const history = histories.find((history) => isEqualAddress(history.validator, publicKey))
      return {
        timestamp: submission.timestamp,
        kind: submission.kind,
        data: submission.data,
        canonical: submission.canonical,
        builder: submission.builder,
        submitted: true,
        pubkey: submission.pubkey,
        finish: submission.finish,
        correct: submission.correct,
        incorrect: submission.incorrect,
        head: submission.correct + submission.incorrect,
        history: histories,
        validated: history ? history.vote : undefined
      }
    })
    .sort((a, b) => a.timestamp - b.timestamp || a.data.localeCompare(b.data))
    .reverse()

  return utterances
}

export const createArchitectCreateCampaignInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  data: CreateCampaignArgs,
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const campaignTitle = data.campaignTitle
  const tag = data.tag
  const open = new BN(data.open)
  const close = new BN(data.close)
  const expire = new BN(data.expire)
  const minimumStake = new BN(100)
  const rpuValidator = new BN(data.rpuValidator * LAMPORTS_PER_USDC)
  const minPhrase = data.minPhrase
  const minValidate = data.minValidate
  const majorityQuorum = data.majorityQuorum
  const rpuGeneral = new BN(data.rpuGeneral * LAMPORTS_PER_USDC)
  const rpuSpecific = new BN(data.rpuSpecific * LAMPORTS_PER_USDC)
  const rpuCause = new BN(data.rpuCause * LAMPORTS_PER_USDC)
  const rpuEffect = new BN(data.rpuEffect * LAMPORTS_PER_USDC)
  const organizer = data.organizer
  const language = data.language
  const industry = data.industry
  const domain = data.domain
  const subject = data.subject

  const minSpecific = new BN(data.minSpecific)
  const minGeneral = new BN(data.minGeneral)
  const minCause = new BN(data.minCause)
  const minEffect = new BN(data.minEffect)
  const acceptRate = 60

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [campaignVault] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:VAULT'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [farmConfigAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const farmConfig = await FarmConfig.fromAccountAddress(connection, farmConfigAccount, 'processed')
  const userToken = getAssociateTokenAccount(args.snsMint, publicKey)
  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [priceFeed] = PublicKey.findProgramAddressSync(
    [Buffer.from('PRICE:FEED:CONFIG'), Buffer.from(SNS_PAIR)],
    farmConfig.oracle
  )
  const [dyfVault] = PublicKey.findProgramAddressSync([pdaAccount.toBuffer(), Buffer.from('DYF:VAULT')], args.programId)

  instructions.push(
    createCreateCampaignInstruction(
      {
        user: publicKey,
        campaignAccount,
        campaignVault,
        dyfVault,
        userToken,
        mint: args.snsMint,
        farmConfig: farmConfigAccount,
        priceFeed,
        pdaAccount,
        campaignTableAccount: farmConfig.campaignsTables,
        lookupProgram: new PublicKey(LOOKUP_PROGRAM_ADDRESS),
        oracle: farmConfig.oracle,
        clock: SYSVAR_CLOCK_PUBKEY,
        rent: SYSVAR_RENT_PUBKEY
      },
      {
        campaignTitle: campaignTitle,
        pair: SNS_PAIR,
        industry: industry,
        domain: domain,
        subject: subject,
        organizer: organizer,
        lang: language,
        kind: tag,
        open: open,
        close: close,
        expire: expire,
        rpuValidator: rpuValidator,
        rpuGeneral: rpuGeneral,
        rpuSpecific: rpuSpecific,
        rpuCause: rpuCause,
        rpuEffect: rpuEffect,
        minBuilder: minPhrase,
        minValidator: minValidate,
        majorityQuorum: majorityQuorum,
        minimumStake,
        minGeneral,
        minSpecific,
        minCause,
        minEffect,
        acceptRate
      },
      args.programId
    )
  )
  return { instructions, signers }
}

export const createArchitectUpdateCampaignInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  data: UpdateCampaignArgs,
  args: {
    programId: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const campaignTitle = data.campaignTitle
  const open = new BN(data.open)
  const close = new BN(data.close)
  const expire = new BN(data.expire)

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(data.campaignTitle)],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)

  instructions.push(
    createUpdateCampaignInstruction(
      {
        user: publicKey,
        campaignAccount,
        farmConfig
      },
      {
        campaignTitle: campaignTitle,
        open,
        close,
        expire
      },
      args.programId
    )
  )

  return { instructions, signers }
}

