export const getUnusedCampaignTitle = async (
  publicKey: PublicKey,
  connection: Connection,
  args: { apiHost: string; apiAuth: string; programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  let campaignTitle = ''
  let ret = 1
  while (ret !== 2) {
    if (ret === 1) {
      ret = 0
      const str = randomstring.generate(16)

      const campaignTitles: string[] = await getAllCampaignTitles({
        apiHost: args.apiHost,
        apiAuth: args.apiAuth
      })
      if (campaignTitles.includes(str)) {
        ret = 1
      } else {
        const [campaignAccount] = PublicKey.findProgramAddressSync(
          [Buffer.from('DYF:CAMPAIGN'), Buffer.from(str)],
          args.programId
        )

        const campaignAccountInfo = await connection.getParsedAccountInfo(campaignAccount)
        if (!campaignAccountInfo || !campaignAccountInfo.value) {
          ret = 2
          campaignTitle = str
        } else {
          ret = 1
        }
      }
    }
  }

  return campaignTitle
}
