import * as splToken from '@solana/spl-token'
import {
  Connection,
  PublicKey,
  Signer,
  TransactionInstruction,
  Transaction,
  Commitment,
  SimulatedTransactionResponse,
  SendOptions,
  RpcResponseAndContext,
  TransactionSignature,
  SignatureStatus,
  AccountInfo
} from '@solana/web3.js'
import { AnchorWallet } from '../typings'
import { sleep } from './helper'
import bs58 from 'bs58'

const DEFAULT_TIMEOUT = 3 * 60 * 1000 // 3 minutes

export async function sendInstructions(
  connection: Connection,
  wallet: AnchorWallet,
  instructions: TransactionInstruction[],
  signers: Signer[],
  commitment: Commitment = 'processed',
  payer: PublicKey = wallet.publicKey
): Promise<string> {
  let tx = new Transaction()
  tx.recentBlockhash = (await connection.getRecentBlockhash()).blockhash
  tx.feePayer = payer || wallet.publicKey
  tx.add(...instructions)
  if (signers.length > 0) {
    tx.partialSign(...signers)
  }
  tx = await wallet.signTransaction(tx)

  try {
    const { txid } = await sendAndConfirmWithRetry(
      connection,
      tx.serialize(),
      {
        skipPreflight: true
      },
      commitment
    )
    return txid
  } catch (e) {
    console.error(e)
    throw e
  }
}

function getUnixTime(): number {
  return new Date().valueOf() / 1000
}

export const awaitTransactionSignatureConfirmation = async (
  txid: TransactionSignature,
  timeout: number,
  connection: Connection,
  commitment: Commitment = 'recent',
  queryStatus = false
): Promise<SignatureStatus | null | void> => {
  let done = false
  let status: SignatureStatus | null | void = {
    slot: 0,
    confirmations: 0,
    err: null
  }
  let subId = 0
  // eslint-disable-next-line no-async-promise-executor
  status = await new Promise(async (resolve, reject) => {
    setTimeout(() => {
      if (done) {
        return
      }
      done = true
      console.log('Rejecting for timeout...')
      reject({ timeout: true })
    }, timeout)
    try {
      console.log('COMMIMENT', commitment)
      subId = connection.onSignature(
        txid,
        (result: any, context: any) => {
          done = true
          status = {
            err: result.err,
            slot: context.slot,
            confirmations: 0
          }
          if (result.err) {
            console.log('Rejected via websocket', result.err)
            reject(status)
          } else {
            console.log('Resolved via websocket', result)
            resolve(status)
          }
        },
        commitment
      )
    } catch (e) {
      done = true
      console.error('WS error in setup', txid, e)
    }
    while (!done && queryStatus) {
      // eslint-disable-next-line no-loop-func
      ;(async () => {
        try {
          const signatureStatuses = await connection.getSignatureStatuses([txid])
          status = signatureStatuses && signatureStatuses.value[0]
          if (!done) {
            if (!status) {
              console.log('REST null result for', txid, status)
            } else if (status.err) {
              console.log('REST error for', txid, status)
              done = true
              reject(status.err)
            } else if (!status.confirmations && !status.confirmationStatus) {
              console.log('REST no confirmations for', txid, status)
            } else {
              console.log('REST confirmation for', txid, status)
              if (!status.confirmationStatus || status.confirmationStatus == commitment) {
                done = true
                resolve(status)
              }
            }
          }
        } catch (e) {
          if (!done) {
            console.log('REST connection error: txid', txid, e)
          }
        }
      })()
      await sleep(2000)
    }
  })

  if (
    //@ts-ignore
    connection._signatureSubscriptions &&
    //@ts-ignore
    connection._signatureSubscriptions[subId]
  ) {
    connection.removeSignatureListener(subId)
  }
  done = true
  console.log('Returning status ', status)
  return status
}

async function simulateTransaction(
  connection: Connection,
  transaction: Transaction,
  commitment: Commitment
): Promise<RpcResponseAndContext<SimulatedTransactionResponse>> {
  // @ts-ignore
  transaction.recentBlockhash = await connection._recentBlockhash(
    // @ts-ignore
    connection._disableBlockhashCaching
  )

  const signData = transaction.serializeMessage()
  // @ts-ignore
  const wireTransaction = transaction._serialize(signData)
  const encodedTransaction = wireTransaction.toString('base64')
  const config: any = { encoding: 'base64', commitment }
  const args = [encodedTransaction, config]

  // @ts-ignore
  const res = await connection._rpcRequest('simulateTransaction', args)
  if (res.error) {
    throw new Error('failed to simulate transaction: ' + res.error.message)
  }
  return res.result
}

export async function sendAndConfirmWithRetry(
  connection: Connection,
  txn: Buffer,
  sendOptions: SendOptions,
  commitment: Commitment,
  timeout = DEFAULT_TIMEOUT
): Promise<{ txid: string }> {
  let done = false
  // let slot = 0;
  const txid = await connection.sendRawTransaction(txn, sendOptions)
  const startTime = getUnixTime()
  ;(async () => {
    while (!done && getUnixTime() - startTime < timeout) {
      await connection.sendRawTransaction(txn, sendOptions)

      await sleep(500)
    }
  })()
  try {
    const confirmation = await awaitTransactionSignatureConfirmation(txid, timeout, connection, commitment, true)

    if (!confirmation) throw new Error('Timed out awaiting confirmation on transaction')

    if (confirmation.err) {
      const tx = await connection.getTransaction(txid)
      console.error(tx?.meta?.logMessages?.join('\n'))
      console.error(confirmation.err)
      throw new Error('Transaction failed: Custom instruction error')
    }

    // slot = confirmation?.slot || 0;
  } catch (err) {
    console.error('Timeout Error caught', err)
    if (err.timeout) {
      throw new Error('Timed out awaiting confirmation on transaction')
    }
    let simulateResult: SimulatedTransactionResponse | null = null
    try {
      simulateResult = (await simulateTransaction(connection, Transaction.from(txn), 'single')).value
    } catch (e) {
      const tx = await connection.getTransaction(txid)
      console.error(tx?.meta?.logMessages?.join('\n'))
    }
    if (simulateResult && simulateResult.err) {
      if (simulateResult.logs) {
        console.error(simulateResult.logs.join('\n'))
      }
    }

    if (err.err) {
      throw err.err
    }

    throw err
  } finally {
    done = true
  }

  console.log('Latency', txid, getUnixTime() - startTime)

  return { txid }
}

export const getAssociateTokenAccount = (mint: PublicKey, authority: PublicKey): PublicKey => {
  const [address] = PublicKey.findProgramAddressSync(
    [authority.toBuffer(), splToken.TOKEN_PROGRAM_ID.toBuffer(), mint.toBuffer()],
    splToken.ASSOCIATED_TOKEN_PROGRAM_ID
  )
  return address
}

export const getOrCreateAssociateTokenAccount = async (
  connection: Connection,
  wallet: AnchorWallet,
  mint: PublicKey,
  authority: PublicKey
): Promise<PublicKey> => {
  const ata = getAssociateTokenAccount(mint, authority)
  const accountInfo = await connection.getAccountInfo(ata)
  if (accountInfo == null) {
    const instructions = [
      splToken.createAssociatedTokenAccountInstruction(
        wallet.publicKey,
        ata,
        authority,
        mint,
        splToken.TOKEN_PROGRAM_ID,
        splToken.ASSOCIATED_TOKEN_PROGRAM_ID
      )
    ]
    await sendInstructions(connection, wallet, instructions, [])
  }
  return ata
}

export const isEqualAddress = (acc_st: string | PublicKey, acc_nd: string | PublicKey): boolean => {
  if (acc_st === null || acc_st === undefined || acc_nd === null || acc_nd === undefined) return false

  const pubkey_st = typeof acc_st === 'string' ? acc_st : new PublicKey(acc_st).toBase58()
  const pubkey_nd = typeof acc_nd === 'string' ? acc_nd : new PublicKey(acc_nd).toBase58()

  return pubkey_st === pubkey_nd
}

export const isIncludingAddress = (arr_addr: Array<string | PublicKey>, addr: string | PublicKey): boolean => {
  return arr_addr.filter((addr_st) => isEqualAddress(addr, addr_st)).length > 0
}

export const isValidAddress = (acc: string | PublicKey): boolean => {
  const pubkey = typeof acc === 'string' ? new PublicKey(acc) : acc
  return PublicKey.isOnCurve(pubkey.toBuffer())
}

export const isEmptyAddress = (acc: string | PublicKey): boolean => {
  const pubkey = typeof acc === 'string' ? new PublicKey(acc) : acc

  return pubkey.toBase58() === '11111111111111111111111111111111'
}

export const getAccountsByDiscriminator = async (
  connection: Connection,
  discriminator: Uint8Array,
  programId: PublicKey
): Promise<
  {
    pubkey: PublicKey
    account: AccountInfo<Buffer>
  }[]
> => {
  const accounts = await connection.getProgramAccounts(programId, {
    filters: [
      {
        memcmp: { offset: 0, bytes: bs58.encode(discriminator) }
      }
    ]
  })
  return accounts
}
