import { Connection, LAMPORTS_PER_SOL, PublicKey } from '@solana/web3.js'
import BN from 'bn.js'
import base58 from 'bs58'
import _ from 'lodash'
import {
  Campaign,
  CampaignActivity,
  campaignActivityDiscriminator,
  campaignDiscriminator,
  FarmConfig,
  Profile,
  Role
} from '../dyfarm'
import { checkWhitelist, getAllCampaignsInfo, getCampaginMeta, getCampaignInfo } from '../services'
import { ApiCampaignInfo, PCampaign } from '../typings'
import { CampaignStatus, decodeText, getAccountsByDiscriminator, isEqualAddress } from '../utils'

export const getCampaignFromCampaignAccount = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; stopOffset: number; deployment: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignMeta = await getCampaginMeta(campaignTitle, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  if (!campaignMeta) return

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignMeta.dapp_title)],
    args.programId
  )

  // @ts-ignore
  let campaign: Campaign = undefined
  try {
    campaign = await Campaign.fromAccountAddress(connection, campaignAccount, 'processed')
  } catch (e) {
    /* empty */
  }
  if (campaign === undefined) return

  const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignMeta.dapp_title), publicKey.toBuffer()],
    args.programId
  )

  let stakeStatus = false
  let role: Role = undefined
  try {
    const campaignActivity = await CampaignActivity.fromAccountAddress(connection, campaignActivityAccount, 'processed')
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    role = profile.role
    if (!new BN(campaignActivity.stakeAmount).eqn(0)) stakeStatus = true
  } catch (e) {
    /* empty */
  }

  const status = await getCampaignStatusFromCampaignTitle(
    publicKey,
    connection,
    role ?? Role.Architect,
    campaignTitle,
    {
      programId: args.programId,
      stopOffset: args.stopOffset
    }
  )

  return {
    campaignTitle: campaignMeta.dapp_title,
    tag: campaign.tag,
    pubkey: campaignAccount.toBase58(),
    industry: decodeText(campaign.industry),
    domain: decodeText(campaign.domain),
    subject: decodeText(campaign.subject),
    explain: campaignMeta.explain,
    organizer: decodeText(campaign.organizer),
    language: decodeText(campaign.lang),
    specific: campaignMeta.phrase_specific,
    general: campaignMeta.phrase_general,
    cause: campaignMeta.phrase_cause,
    effect: campaignMeta.phrase_effect,
    open: new BN(campaign.open).toNumber(),
    stop: new BN(campaign.close).toNumber() - args.stopOffset,
    close: new BN(campaign.close).toNumber(),
    expire: new BN(campaign.expire).toNumber(),
    minStake: new BN(campaign.minStake).toNumber(),
    minPhrase: Number(campaign.minPhrase),
    minValidate: Number(campaign.minValidate),
    rpuSpecific: new BN(campaign.rpuSpecific).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuGeneral: new BN(campaign.rpuGeneral).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuCause: new BN(campaign.rpuCause).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuEffect: new BN(campaign.rpuEffect).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuValidator: new BN(campaign.rpuValidator).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    minSpecific: new BN(campaign.minSpecific).toNumber(),
    minGeneral: new BN(campaign.minGeneral).toNumber(),
    minCause: new BN(campaign.minCause).toNumber(),
    minEffect: new BN(campaign.minEffect).toNumber(),
    majorityQuorum: Number(campaign.majorityQuorum),
    architect: campaign.architect.toBase58(),
    finish: campaign.finish,
    utterances: [],
    progress: 0,
    submissions: 0,
    rejections: 0,
    timestamp: Math.round(Date.now() / 1000),
    deployment: args.deployment,
    status,
    stakeStatus
  } as PCampaign
}

export const getCampaignFromCampaignInfo = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  role: Role,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignInfo = await getCampaignInfo(campaignTitle, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  if (!campaignInfo) return

  const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignInfo.campaignTitle), publicKey.toBuffer()],
    args.programId
  )

  let stakeStatus = false
  try {
    const campaignActivity = await CampaignActivity.fromAccountAddress(connection, campaignActivityAccount, 'processed')
    if (!new BN(campaignActivity.stakeAmount).eqn(0)) stakeStatus = true
  } catch (e) {
    /* empty */
  }

  const status = await getCampaignStatusFromCampaignTitle(
    publicKey,
    connection,
    role ?? Role.Architect,
    campaignTitle,
    {
      programId: args.programId,
      stopOffset: args.stopOffset
    }
  )

  return {
    ...campaignInfo,
    stop: campaignInfo.close - args.stopOffset,
    minStake: 100, // @TODO
    utterances: [],
    status,
    stakeStatus
  } as PCampaign
}

export const getAllCampaigns = async (
  publicKey: PublicKey,
  connection: Connection,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; rpcHost: string; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignsInfo: ApiCampaignInfo[] = await getAllCampaignsInfo({
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })

  let role: Role = undefined
  try {
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    role = profile.role
  } catch (e) {
    /* empty */
  }

  const campaignTitles = campaignsInfo.map((campaignInfo) => campaignInfo.campaignTitle)
  const campaignsStatus = await getCampaignStatusFromCampaignTitles(
    publicKey,
    connection,
    role ?? Role.Architect,
    campaignTitles,
    {
      programId: args.programId,
      stopOffset: args.stopOffset
    }
  )

  const campaignActivityAccountsInfo = await connection.getProgramAccounts(args.programId, {
    filters: [
      {
        memcmp: { offset: 0, bytes: base58.encode(campaignActivityDiscriminator) }
      },
      {
        memcmp: { offset: 8, bytes: base58.encode(publicKey.toBytes()) }
      }
    ]
  })
  const campaignActivities = campaignActivityAccountsInfo.map((accountInfo) => {
    return CampaignActivity.deserialize(accountInfo.account.data)[0]
  })

  const campaigns: PCampaign[] = campaignsInfo.map((campaignInfo) => {
    const campaignActivity = campaignActivities.find((row) => isEqualAddress(row.campaign, campaignInfo.pubkey))
    const stakeStatus = campaignActivity ? !new BN(campaignActivity.stakeAmount).eqn(0) : false

    return {
      ...campaignInfo,
      stop: campaignInfo.close - args.stopOffset,
      minStake: 100, // @TODO
      utterances: [],
      status: campaignsStatus.find((row) => row.campaignTitle === campaignInfo.campaignTitle).status,
      stakeStatus: role === undefined ? false : stakeStatus
    }
  })

  return campaigns
}

export const getAppRole = async (publicKey: PublicKey, args: { apiHost: string; apiAuth: string }) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  const role = await checkWhitelist(publicKey.toBase58(), {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  return role === null ? undefined : ((Role[_.capitalize(role)] ? Role[_.capitalize(role)] : Role.Builder) as Role)
}

export const getCampaignStatusFromCampaignTitle = async (
  publicKey: PublicKey,
  connection: Connection,
  role: Role,
  campaignTitle: string,
  args: { programId: PublicKey; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const [farmConfigAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const farmConfig = await FarmConfig.fromAccountAddress(connection, farmConfigAccount, 'processed')

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )

  try {
    const campaign = await Campaign.fromAccountAddress(connection, campaignAccount, 'processed')

    return getCampaignStatusFromCampaignAccount(campaign, farmConfig, role, { stopOffset: args.stopOffset })
  } catch (e) {
    return CampaignStatus.Closed
  }
}

export const getCampaignStatusFromCampaignTitles = async (
  publicKey: PublicKey,
  connection: Connection,
  role: Role,
  campaignTitles: string[],
  args: { programId: PublicKey; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const [farmConfigAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const farmConfig = await FarmConfig.fromAccountAddress(connection, farmConfigAccount, 'processed')

  const campaignAccountsInfo = await getAccountsByDiscriminator(
    connection,
    new Uint8Array(campaignDiscriminator),
    args.programId
  )

  const liveCampaigns = campaignAccountsInfo
    .map((accountInfo) => {
      try {
        return Campaign.deserialize(accountInfo.account.data)[0]
      } catch (e) {
        return null
      }
    })
    .filter((row) => !_.isNull(row))
  const liveCampaignsStatus = liveCampaigns.map((campaign) => {
    return {
      campaignTitle: decodeText(campaign.title),
      status: getCampaignStatusFromCampaignAccount(campaign, farmConfig, role, { stopOffset: args.stopOffset })
    }
  })

  return campaignTitles.map((campaignTitle) => {
    const live = liveCampaignsStatus.find((row) => row.campaignTitle === campaignTitle)
    if (live) return live
    else
      return {
        campaignTitle,
        status: CampaignStatus.Closed
      }
  })
}

export const getCampaignStatusFromCampaignAccount = (
  campaign: Campaign,
  farmConfig: FarmConfig,
  role: Role,
  args: { stopOffset: number }
): CampaignStatus => {
  const timenow = Math.floor(Date.now() / 1000)

  const open = new BN(campaign.open).toNumber()
  const stop = new BN(campaign.close).toNumber() - args.stopOffset
  const close = new BN(campaign.close).toNumber()
  const finish = campaign.finish
  const finishTime = new BN(campaign.finishTime).toNumber()
  const claimPeriod = new BN(farmConfig.claimPeriod).toNumber()
  const rpcClosePeriod = new BN(farmConfig.rpcClosePeriod).toNumber()
  if (timenow <= open) {
    return CampaignStatus.Upcoming
  }

  if (finish && finishTime < close) {
    // Completed_Campaign
    if (timenow <= finishTime + claimPeriod) {
      return CampaignStatus.FinishedClaimable
    } else if (timenow <= finishTime + claimPeriod + rpcClosePeriod) {
      return CampaignStatus.ClosingByRpc
    } else {
      return CampaignStatus.ClosingByArchitect
    }
  } else if (!finish) {
    // Incompleted_Campaign
    if (timenow <= close) {
      if (role === Role.Builder) {
        if (timenow <= stop) {
          return CampaignStatus.Inprogress
        } else {
          return CampaignStatus.FinishedUnclaimable
        }
      } else {
        return CampaignStatus.Inprogress
      }
    } else {
      return CampaignStatus.FinishedClaimable
    }
  } else {
    // Incompleted_Campaign
    if (timenow <= finishTime + claimPeriod) {
      return CampaignStatus.FinishedClaimable
    } else if (timenow <= finishTime + claimPeriod + rpcClosePeriod) {
      return CampaignStatus.ClosingByRpc
    } else {
      return CampaignStatus.ClosingByArchitect
    }
  }
}
