export const encodeText = (txt: string): Uint8Array => {
  return new TextEncoder().encode(txt)
}

export const decodeText = (bytes: number[]): string => {
  return new TextDecoder('utf-8').decode(Uint8Array.from(bytes.filter((item) => item !== 0)))
}

export const sleep = (ms: number): Promise<unknown> => {
  return new Promise((resolve) => setTimeout(resolve, ms))
}

export async function runPromisesSequentially<T>(functions: (() => Promise<T>)[]): Promise<T[]> {
  if (functions.length === 0) {
    return []
  }
  const [first, ...rest] = functions

  return [await first(), ...(await runPromisesSequentially(rest))]
}
