import axios from 'axios'
import { RpcBatchSubmissionResponse, RpcSubmissionsInfo, RpcValidationsInfo } from '../typings'
import { RpcTxnStatus } from '../utils'

interface RpcApiResponse {
  data: {
    jsonrpc: string
    result?: any
    error?: any
    id: number
  }
  status: number
}

export const rpcRequestAuth = async (wallet: string, args: { rpcHost: string }): Promise<string | null> => {
  return axios
    .post(args.rpcHost, {
      jsonrpc: '2.0',
      method: 'requestAuth',
      params: [wallet],
      id: 1
    })
    .then((response: RpcApiResponse) => {
      if (response.data.error) {
        throw new Error(response.data.error.message)
      }
      if (response.status === 200 && response.data.result) {
        return response.data.result as string
      } else {
        return null
      }
    })
}

