export const rpcSubmitPhrase = async (
  token: string,
  dyfarmContract: string,
  campaignTitle: string,
  wallet: string,
  offchainReference: string,
  offchainType: number,
  kind: number,
  id: number,
  args: { rpcHost: string }
): Promise<string | null> => {
  return axios
    .post(args.rpcHost, {
      jsonrpc: '2.0',
      method: 'SubmitPhrase',
      params: [token, dyfarmContract, campaignTitle, wallet, offchainReference, offchainType, kind],
      id: id
    })
    .then((response: RpcApiResponse) => {
      if (response.data.error) {
        throw new Error(response.data.error.message)
      }
      if (response.status === 200 && response.data.result) {
        return response.data.result as string
      } else {
        return null
      }
    })
}

export const rpcBatchSubmitPhrases = async (
  token: string,
  dyfarmContract: string,
  campaignTitle: string,
  wallet: string,
  offchainReferences: string[],
  offchainTypes: number[],
  kinds: number[],
  args: { rpcHost: string }
): Promise<RpcBatchSubmissionResponse[]> => {
  if (offchainReferences.length !== offchainTypes.length || offchainReferences.length !== kinds.length)
    throw new Error('Something is wrong. Invalid data.')

  const data = offchainReferences.map((offchainReference, idx) => {
    return {
      jsonrpc: '2.0',
      method: 'SubmitPhrase',
      params: [token, dyfarmContract, campaignTitle, wallet, offchainReference, offchainTypes[idx], kinds[idx]],
      id: idx + 1
    }
  })

  return axios.post(args.rpcHost, data).then((response: any) => {
    return response.data.map((row: any) => {
      if (row.error) {
        return {
          isFailed: true,
          error: row.error.message
        }
      }
      return {
        isFailed: false,
        uuid: row.result as string
      }
    })
  })
}

