export const rpcBatchValidatePhrase = async (
  token: string,
  dyfarmContract: string,
  campaignTitle: string,
  wallet: string,
  builders: string[],
  phraseAccounts: string[],
  canonicals: string[],
  values: boolean[],
  confidences: number[],
  args: { rpcHost: string }
): Promise<RpcBatchSubmissionResponse[]> => {
  if (phraseAccounts.length !== canonicals.length || phraseAccounts.length !== values.length)
    throw new Error('Something is wrong. Invalid data.')

  const data = phraseAccounts.map((phraseAccount, idx) => {
    return {
      jsonrpc: '2.0',
      method: 'ValidatePhrase',
      params: [
        token,
        dyfarmContract,
        campaignTitle,
        wallet,
        builders[idx],
        phraseAccount,
        canonicals[idx],
        confidences[idx],
        values[idx]
      ],
      id: idx + 1
    }
  })

  return axios.post(args.rpcHost, data).then((response: any) => {
    return response.data.map((row: any) => {
      if (row.error) {
        return {
          isFailed: true,
          error: row.error.message
        }
      }
      return {
        isFailed: false,
        uuid: row.result as string
      }
    })
  })
}

