export const getRpcSubmissionStatus = async (
  wallet: string,
  campaign: string,
  args: { rpcHost: string }
): Promise<RpcSubmissionsInfo[]> => {
  return axios
    .post(args.rpcHost, {
      jsonrpc: '2.0',
      method: 'getSubmitStatus',
      params: [wallet, campaign],
      id: 1
    })
    .then((response: RpcApiResponse) => {
      if (response.status === 200 && response.data && response.data.result && Array.isArray(response.data.result)) {
        return response.data.result.map((submission) => {
          return {
            uuid: submission.uid,
            canonical: submission.reference,
            status:
              submission.state === 'Expired'
                ? RpcTxnStatus.Expired
                : submission.state === 'Finalized'
                ? RpcTxnStatus.Finalized
                : RpcTxnStatus.Pending
          }
        })
      } else {
        return []
      }
    })
}

export const getRpcValidationStatus = async (
  wallet: string,
  campaign: string,
  args: { rpcHost: string }
): Promise<RpcValidationsInfo[]> => {
  return axios
    .post(args.rpcHost, {
      jsonrpc: '2.0',
      method: 'getValidateStatus',
      params: [wallet, campaign],
      id: 1
    })
    .then((response: RpcApiResponse) => {
      if (response.status === 200 && response.data && response.data.result && Array.isArray(response.data.result)) {
        return response.data.result.map((validation) => {
          return {
            uuid: validation.uid,
            utterance: validation.reference,
            status:
              validation.state === 'Expired'
                ? RpcTxnStatus.Expired
                : validation.state === 'Finalized'
                ? RpcTxnStatus.Finalized
                : RpcTxnStatus.Pending
          }
        })
      } else {
        return []
      }
    })
}

export const getRpcListActivity = async (
  dyfarmContract: string,
  wallet: string,
  args: { rpcHost: string }
): Promise<string[]> => {
  return axios
    .post(args.rpcHost, {
      jsonrpc: '2.0',
      method: 'ListActivity',
      params: [dyfarmContract, wallet],
      id: 1
    })
    .then((response: RpcApiResponse) => {
      if (response.status === 200 && response.data && response.data.result && Array.isArray(response.data.result)) {
        return response.data.result
          .filter((row) => row.stake_status === true && row.campaign !== null && row.campaign !== '')
          .map((row) => String(row.campaign))
      } else {
        return []
      }
    })
}
