import { Connection, PublicKey, Signer, SYSVAR_CLOCK_PUBKEY, TransactionInstruction } from '@solana/web3.js'
import * as splToken from '@solana/spl-token'
import { getAssociateTokenAccount } from '../utils'
import { createAirdropInstruction } from '../dyfarm'

export const createAirdropSNSInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [dyfVault] = PublicKey.findProgramAddressSync([pdaAccount.toBuffer(), Buffer.from('DYF:VAULT')], args.programId)
  let _tokenBalance = null
  const dyfVaultTokenAccount = await connection.getParsedAccountInfo(dyfVault)
  if (dyfVaultTokenAccount.value) {
    // @ts-ignore
    _tokenBalance = dyfVaultTokenAccount.value.data.parsed.info.tokenAmount.uiAmount
  }
  if (_tokenBalance === null || _tokenBalance < 100) {
    throw new Error('The token vault is empty. Please contact the administrator.')
  }

  const userToken = getAssociateTokenAccount(args.snsMint, publicKey)
  const accountInfo = await connection.getAccountInfo(userToken)
  if (accountInfo == null) {
    instructions.push(
      splToken.createAssociatedTokenAccountInstruction(
        publicKey,
        userToken,
        publicKey,
        args.snsMint,
        splToken.TOKEN_PROGRAM_ID,
        splToken.ASSOCIATED_TOKEN_PROGRAM_ID
      )
    )
  }

  instructions.push(
    createAirdropInstruction(
      {
        authority: publicKey,
        userToken,
        tokenVault: dyfVault,
        pdaAccount: pdaAccount,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      args.programId
    )
  )

  return { instructions, signers }
}
