const KANON_NFT_CHARITY_ADDRESS = 'CRTegTRWPceXBc7ywwP9NKHXavjcW5HYaN4AxR3AcH9H'
const DYF_API_HOST = 'https://dyf-dev.synesis.xyz'
const DYF_API_AUTH = 'Basic YXBpdXNlcjphYmNkZWZnaDEyMzQ1Njc4' // "apiuser:abcdefgh12345678"
const DYF_RPC_HOST = 'https://dyf-rpc-dev.synesis.xyz/'
