  PROGRAM_ID: PublicKey = PROGRAM_ID
  SNS_MINT: PublicKey = new PublicKey(SNS_ADDRESS)
  KANON_NFT_CHARITY: PublicKey = new PublicKey(KANON_NFT_CHARITY_ADDRESS)
